export const CleanDataManageRules = {
        mould_name: [
            { required: true, message: "", trigger: "blur" },
        ],
        mould_table: [
            { required: true, message: "", trigger: "blur" },
        ],
        is_enable: [
            { required: true, message: "", trigger: "change" },
        ],
    }
export function  isNum(rule, value, callback) {
    const age = /^[0-9]*$/;
    if (value) {
      if (!age.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    } else {
      callback();
    }
  }